<template>
  <navbar @searchValue=" search = $event" :lang="lang" @changeLang="changeLang" />
  <notes :notes="filterNotes" @delNote="delNote" @changeNote="changeNote" :lang="lang" />
  <modal v-show="modalOpen" @closeModal="clickBtnOut" :currentId="currentId" @addNote="addNote" @delNote="delNote"
    :edit="edit" :editNote="editNote" @editedNote="editedNote" :lang="lang" />
  <addbtn @btnClick="clickBtnIn" />
</template>

<script>
import navbar from "@/components/Navbar.vue";
import notes from "@/components/Notes.vue";
import modal from "@/components/Modal.vue";
import addbtn from "@/components/AddButton.vue";
import langs from "@/lang";

export default {
  components: {
    navbar,
    notes,
    modal,
    addbtn,
  },
  data() {
    return {
      currentId: 1,
      edit: false,
      editNote: {},
      notes: [],
      modalOpen: false,
      search: '',
      lang: 'ru',
      langWords: {},
    };
  },
  created() {
    this.updateNotes();
    localStorage.lang = localStorage.lang || 'ru';
    this.lang = localStorage['lang'] || 'ru';
  },
  beforeCreate() {
    this.langwords = langs;
    localStorage.words = JSON.stringify(this.langwords);
  },
  provide() {
    return { words: JSON.parse(localStorage.words) };
  },
  watch: {
    notes: {
      handler(newNotes) {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    }
  },
  computed: {
    filterNotes() {
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes;
    }
  },
  methods: {
    clickBtnIn() {
      this.modalOpen = true;
      this.edit = false
    },
    clickBtnOut(status) {
      this.modalOpen = status;
      setTimeout(function () {
        this.edit = false;
      }, 500);
    },
    addNote(noteObject) {
      this.notes.push(noteObject);
      this.modalOpen = false;
    },
    delNote(idToDel) {
      let index = this.notes.findIndex(note => note.id == idToDel);
      this.notes.splice(index, 1);
    },
    updateNotes() {
      let localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
        this.currentId = this.notes.length;
        this.currentId++;
      }
    },
    changeNote(idToChange) {
      this.edit = this.modalOpen = true;
      let pickedNoteId = this.notes.find(note => note.id == idToChange);
      this.editNote = pickedNoteId;
    },
    editedNote(editedValue) {
      this.notes.forEach(note => {
        if (note.id == editedValue.id) {
          note.title = editedValue.title
          note.desc = editedValue.desc
          note.date = editedValue.date
        }
      })
    },
    changeLang(val) {
      this.lang = localStorage.lang = val;
    }
  },
};
</script>

<style></style>